.list{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    list-style-type: none;
    padding: 0;
}

.list li a {
    height: 22.5rem;
    width: 20rem;
    border: 1px solid whitesmoke;
    border-radius: 1rem;
    margin: 0.5rem;
    display: flex;
    flex-direction:column;
    overflow: hidden;
    color: orangered;
} 

.image{
   object-fit: cover;
   height: 14.5rem; 
}

.content{
    margin-top: 0.3rem;
    padding: 0.5rem 1rem;
    position: relative;
    height: 7rem;
}

.favorite{
    position: absolute;
    top: 0.5rem;
    right: 1rem;
    font-size: 1.2rem;
}

.favorite.not{
    color: grey;
}


.stars{
    margin: 0.5rem 0;
}

.product_item_footer{
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
}

.origins span{
    border-radius: 2rem;
    background-color: whitesmoke;
    display: inline-block;
    font-size: 0.8rem;
    color: brown;
    margin-right: 0.3rem;
    padding: 0 0.5rem;
}