.container{
    display: flex;
    justify-content: center;
    align-items: center;
    flex: wrap;
    margin: 3rem;
}

.container > *{
    min-width: 25rem;
    max-width: 40rem;
}


.image{
    border-radius: 3rem;
    flex: 1 0;
    object-fit: cover;
    height:35rem;
    margin:1rem;
}

.details{
    width: 100%;
    display: flex;
    flex-direction: column;
    flex: 1 0;
    border-radius: 3rem;
    color: black;
    margin-left: 1rem;
}

.header{
    display: flex;
    justify-content: space-between;
}

.name{
    font-size: 2rem;
    font-weight: bold;
}

.favorite{
    color: #e72929;
    font-size: 2.5rem;
}

.favorite.not{
    color: gray;
}

.origins{
    display: flex;
    flex-wrap: wrap;
    margin: 0.7rem 0;
}

.origins > span{
    padding: 0.5rem;
    font-size: 1.2rem;
    margin: 0.5rem 0.5rem 0 0;
    border-radius: 2rem;
    background-color: aliceblue;
}

.cook_time span{
    padding: 0.4rem 0.1rem 0.6rem;
    border-radius:10rem;
    font-size: 1.3rem;
}

.price{
    font-size: 1.8rem;
    margin: 2rem 2rem 2rem 0;
    color: darkgreen;

}

.price::before{
    content:'Price:';
    color: darkgray;
}

.container button{
    color:whitesmoke;
    background-color: #e72929;
    border: none;
    font-size: 1.2rem;
    padding: 1rem;
    border-radius: 10rem;
    outline: none;
}

.container button:hover{
    opacity: 0.9;
    cursor: pointer;
}